html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: var(--font-inter);
  margin: 0;
  padding: 0;
  background-color: var(--background);
  color: var(--text-color2);
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

h1,
h2,
h3 {
  margin-top: 0;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  color: var(--text-color1);
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --toggle-btn-light: hsl(228, 46%, 96%);
  --dark-bg-blue: hsl(230, 17%, 14%);
  --dark-bg-blue-top: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-bg-top: hsl(225, 100%, 98%);
  --light-card: hsl(228, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --background: var(--light-bg);
  --text-color1: var(--light-text1);
  --text-color2: var(--light-text2);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle-bg: var(--toggle);
  --toggle-btn: var(--toggle-btn-light);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg-blue);
    --text-color1: var(--dark-text1);
    --text-color2: var(--dark-text2);
    --card-bg: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --toggle-bg: linear-gradient(
      225deg,
      var(--toggle-bg-end) 0%,
      var(--toggle-bg-start) 98.02%
    );
    --toggle-btn: var(--dark-bg-blue);
  }
}
body.light {
  --background: var(--light-bg);
  --text-color1: var(--light-text1);
  --text-color2: var(--light-text2);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle-bg: var(--toggle);
  --toggle-btn: var(--toggle-btn-light);
}

body.dark {
  --background: var(--dark-bg-blue);
  --text-color1: var(--dark-text1);
  --text-color2: var(--dark-text2);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle-bg: linear-gradient(
    225deg,
    var(--toggle-bg-end) 0%,
    var(--toggle-bg-start) 98.02%
  );
  --toggle-btn: var(--dark-bg-blue);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  margin-top: 2.25rem;
}
@media (min-width: 48em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__subtittle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color1);
}
.toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  border: none;
}
@media (max-width: 47.99875em) {
  .toggle {
    justify-content: space-between;
    margin-top: 2rem;
  }
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color1);
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
}
.toggle input[type=radio] {
  opacity: 0;
  height: 1.5rem;
  width: 1.5rem;
  margin: 0 -0.125rem;
  cursor: pointer;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid white;
}
.toggle__background {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  border-radius: 0.75rem;
  background: var(--toggle-bg);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  height: 1.125rem;
  width: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-btn);
  transition: all 250ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  right: 0.1875rem;
  left: calc(100% - 1.3125rem);
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 48em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  background: var(--card-bg);
  color: var(--text-color2);
  padding: 1.5625rem;
  text-align: center;
  border-radius: 0.3125rem;
  cursor: pointer;
}
.card:hover {
  background: var(--card-hover);
  transition-duration: 250ms;
}
.card--facebook {
  border-top: 0.3125rem solid var(--facebook);
}
.card--instagram {
  position: relative;
  overflow: hidden;
  padding-top: 1.875rem;
}
.card--instagram::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 0.3125rem;
  width: 100%;
  background: linear-gradient(225deg, var(--instagram-start), var(--instagram-end));
}
.card--twitter {
  border-top: 0.3125rem solid var(--twitter);
}
.card--youtube {
  border-top: 0.3125rem solid var(--youtube);
}
.card__icon {
  margin-right: 0.5rem;
}
.card__platform {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1.25rem;
  margin-bottom: 1.75rem;
}
.card__username {
  color: var(--text-color1);
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  color: var(--text-color2);
  margin-bottom: 1.5625rem;
}
.card__count {
  color: var(--text-color2);
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  margin-bottom: 0.25rem;
  align-self: end;
}
.card__count--big {
  font-size: 3.5rem;
}
.card__count--small {
  font-size: 2rem;
  margin: 0;
}
.card__label {
  color: var(--dark-text1);
  font-size: 0.75rem;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 0.3125rem;
}
.card__change {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.75rem;
  font-weight: 700;
  align-self: end;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change--align-right {
  justify-self: end;
}
.card__number {
  margin-left: 0.25rem;
}
.card__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  justify-items: start;
  align-items: center;
  gap: 1.4375rem;
}
.card__grid img {
  justify-self: end;
}
.card__subtitle {
  color: var(--text-color1);
  font-size: 0.875rem;
  font-weight: 700;
}